@media screen and (max-width: 1550px) and (min-width: 1001px) {  




    .buySellView {
        width: 100%;
        height: 100vh;
    }
    
    .buySellView_middle {
        width: 100%;
        height: 80%;
        background-image: url("../../../image_assets/general/background_image.webp");
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        justify-content: center; 
    }
    
    .buySellView_middle_container {
        width: 1024px;
        height: 100%;
        background: rgb(190,146,116);
        background: radial-gradient(circle, rgba(190,146,116,1) 0%, rgba(109,89,76,1) 100%);
        z-index: 100;
        
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        row-gap: 10px;
    }
    
    .buySellView_middle_lottery_container {
        width: 750px;
        height: 320px;
        background-color: #2e1e12;
        display: flex;
        justify-content: center;
        gap: 15px;
    }
    
    .buySellView_lottery_box {
        width: 30%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        row-gap: 5px;
    }
    
    .lottery_items {
        width: 100%;
    }
    
    .lottery_btns {
        width: 120px;
        cursor: pointer;
    }
    
    
    
    
    .buySellView_lottery_desc {
        font-size: 17px;
        font-family: 'Luckiest Guy', cursive;
        color: white;
        margin: 10px;
    }
    
    
    .buySellView_or {
        font-size: 40px;
        margin: 0;
        color: white;
        font-family: 'Luckiest Guy', cursive;
        margin-top: 5px;
    }
    
    .buySellView_visit_market {
        width: 200px;
        cursor: pointer;
    /*    pointer-events: none;  */
        opacity: 1;
    }
    
    
    .buySellView_footer {
        width: 100%;
        height: 10%;
        background-color: #2e1e12;
        display: flex;
        justify-content: center;
        position: relative;
        z-index: 200;
    } 
    
    .buySellView_footer_container {
        width: 500px;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 5px;
        position: absolute;
        
    }





}